import axios from 'axios'
import React, { useEffect, useState } from 'react'
import AddNewAdress from './AddNewAdress'
import Swal from 'sweetalert2'

export default function UserAdress({ onSelectAddress }) {
    const[userLoggedAdress,setuserLoggedAdress]= useState([])
    
   async function getLoggedUserAdress() {
    const{data}= await axios.get('https://ecommerce.routemisr.com/api/v1/addresses',
    {headers:{token:localStorage.getItem('token')

}}
)
console.log(data)
setuserLoggedAdress(data.data)
}
const handleClick = (address) => {
  if (onSelectAddress) {
    onSelectAddress(address); // Ensure onSelectAddress is a function before calling
  }
};



useEffect(() => {
    getLoggedUserAdress()

  }, [userLoggedAdress])

  function removeAdress(id) {
    const swalWithBootstrapButtons = Swal.mixin({
      customClass: {
        confirmButton: "btn btn-success",
        cancelButton: "btn btn-danger"
      },
      buttonsStyling: false
    });
    swalWithBootstrapButtons.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: "Yes, delete it!",
      cancelButtonText: "No, cancel!",
      reverseButtons: true
    }).then(async(result) => {
      if (result.isConfirmed) {
        const{data}= await axios.delete('https://ecommerce.routemisr.com/api/v1/addresses/' + id,
        {headers:{token:localStorage.getItem('token')
      
      }})
    
      setuserLoggedAdress(data.data)
    
        swalWithBootstrapButtons.fire({
          title: "Deleted!",
          text: "Your adress has been deleted.",
          icon: "success"
        });
      } else if (
       
        result.dismiss === Swal.DismissReason.cancel
      ) {
        swalWithBootstrapButtons.fire({
          title: "Cancelled",
          text: "Your imaginary file is safe :)",
          icon: "error"
        });
      }
    });
   
}
   
  

  return (
    <div className='w-75 mx-auto mb-5' >
         <AddNewAdress/>
        { userLoggedAdress?.map((adress, index) => (
          <div className='text-start' key={index}>
            
    <div className=' p-3 mt-3 w-100 adress col-md-2  '>
   
    <div className='w-100  d-flex justify-content-end'>
    <button className='btn text-danger mt-3 ' onClick={()=>{removeAdress(adress._id) }}  >Remove <i className=" bg fa-solid fs-4 fa-trash-can"></i></button>
</div>
    <h5 className='pt-2' ><span className='fw-bold'>Name</span> : {adress.name}</h5>

    <h5 className='pt-2' ><span className='fw-bold' >Details</span> : {adress.details}</h5>
    <h5 className='pt-2' ><span className='fw-bold' >City</span> : {adress.city}</h5>
    <h5 className='pt-2' ><span className='fw-bold' >Phone</span> : {adress.phone}</h5>
    <input
              type="radio"
              name="selectedAddress"
              value={adress.name}
              onChange={() => handleClick(adress)}
            />
    

</div>

          </div>)) }
      </div>
  )
}

