
import axios from 'axios';
import { useFormik } from 'formik';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import UserAddress from './UserAdress';
import AddNewAdress from './AddNewAdress';

export default function Address() {
  const [isLoading, setIsLoading] = useState(false);
  const [selectedAddress, setSelectedAddress] = useState(null);
  let { cartId } = useParams();

  const { values, handleChange, handleBlur, handleSubmit } = useFormik({
    initialValues: {
      details: '',
      city: '',
      phone: '',
    },
    onSubmit: async () => {
      try {
        setIsLoading(true);
        let { data } = await axios.post(
          `https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartId}`,
          {
            shippingAddress: values,
          },
          {
            headers: {
              token: localStorage.getItem('token'),
            },
            params: {
              url: 'https://engineertoka.github.io/E-Commerce-shopping-App',
            },
          }
        );
        localStorage.setItem('details', values.details);
        localStorage.setItem('city', values.city);
        localStorage.setItem('phone', values.phone);

        console.log(data);
        window.open(data.session.url, '_self');
      } catch (error) {
        console.log(error.data.status);
      }
      setIsLoading(false);
    },
  });

  const handleAddressSelect = (address) => {
    setSelectedAddress(address);
    // Set form values when address is selected
    handleChange({
      target: {
        name: 'details',
        value: address.details,
      },
    });
    handleChange({
      target: {
        name: 'city',
        value: address.city,
      },
    });
    handleChange({
      target: {
        name: 'phone',
        value: address.phone,
      },
    });
  };
  useEffect(()=>{},[selectedAddress])

  return (
    <>
      <div className="w-75 m-auto my-5 ">
        <h1 className="text-start pt-5">Address:</h1>
        <form onSubmit={handleSubmit}>
          <div className="form-row">
            <div className="form-group">
              <label className="my-1" htmlFor="details">
                Details:
              </label>
              <input
                name="details"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.details}
                type="text"
                className="form-control mb-3"
                id="details"
                placeholder="Enter your details"
              />
            </div>
            <div className="form-group ">
              <label className="my-1" htmlFor="city">
                City:
              </label>
              <input
                name="city"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.city}
                type="text"
                className="form-control mb-3"
                id="city"
                placeholder="Enter your city"
              />
            </div>
            <div className="form-group ">
              <label className="my-1" htmlFor="phone">
                Phone:
              </label>
              <input
                name="phone"
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.phone}
                type="text"
                className="form-control mb-3"
                id="phone"
                placeholder="Enter your Phone"
              />
            </div>
            <div className="w-100 d-flex justify-content-end">
              <button type="submit" className="btn btn-success my-2 me-2">
                Check out
              </button>
            </div>
           
          </div>
   
        </form>
      
      </div>
      
      <section className="address ">
        <div className="container">
          <div className="address-wrapper checkout-wrapper">
            <div className="left-main-wrapper">
              <div className="cart-breadcrumb-wrapper"></div>
              <div className="address-item-wrapper">
             
                <UserAddress onSelectAddress={handleAddressSelect} />
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
